<template>
<div>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <!-- Brand and toggle get grouped for better mobile display -->
      <div class="navbar-header">
        <button @click='hidemenubtn = !hidemenubtn' type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                      <span class="sr-only">Toggle navigation</span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
        </button>
        <router-link to='/'>
        <a class="navbar-brand">中南猎奇</a>
        </router-link>
      </div>
      <!-- Collect the nav links, forms, and other content for toggling -->
      <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
        <ul class="nav navbar-nav">
          <li @click='checklogin'><a href="#">检测是否登录 </a></li>
        </ul>
        <ul class="nav navbar-nav navbar-right">
          <li v-if='haslogin'><a href="#">个人信息 </a></li>
          <li v-if='haslogin' @click='logout'><a href="#">退出 </a></li>
          <li v-if='!haslogin' data-toggle="modal" data-target="#myModal" @click='login'><a href="#">登录 </a></li>
          <li v-if='!haslogin' data-toggle="modal" data-target="#myModal" @click='regis'><a href="#">注册 </a></li>
        </ul>
      </div>
      <!-- /.navbar-collapse -->
      <!-- Button trigger modal -->
    </div>
    <!-- /.container-fluid -->
  </nav>
        <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true" data-backdrop="static">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button id='closemodel' type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
              <h4 class="modal-title" id="myModalLabel">{{ login_title }}</h4>
            </div>
            <div class="modal-body">
              <div class="input-group col-xs-10">
                <span class="input-group-addon">用户名 </span>
                <input v-model='uname' type="text" class="form-control" placeholder="邮箱" aria-describedby="sizing-addon2">
              </div>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">密码 </span>
                <input v-model='psword' type="password" class="form-control" placeholder="英文加数字" aria-describedby="sizing-addon2">
              </div>
              <div v-show='show_nickname'>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">昵称 </span>
                <input v-model='nickname' type="text" class="form-control" placeholder="昵称" aria-describedby="sizing-addon2">
              </div>
              </div>
            </div>
            <div class="modal-footer" id='twobtn'>
              <button type="button" class="btn btn-primary" @click='sub'>{{ login_title }}</button>
              <div class='jumbotron'>调试用<br>{{opt}}</div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  import jquery from 'jquery'
  import connect from '../../static/js/Connector.js'
  export default {
    data () {
      return {
        uname: '',
        psword: '',
        nickname: '',
        show_nickname: false,
        login_title: '',
        ajax_type: '',
        ajax_success: () => {},
        ajax_failed: () => {},
        hidemenubtn: false,
        haslogin: false
      }
    },
    computed: { // 绑定更新ajax_data
      ajax_data: function () {
        if (this.ajax_type === 'USER_REGISTE') {
          return {
            username: this.uname,
            password: this.psword,
            nickname: this.nickname
          }
        } else if (this.ajax_type === 'USER_LOGIN') {
          return {
            username: this.uname,
            password: this.psword
          }
        } else if (this.ajax_type === 'USER_CHECK') {
          return {}
        } else {
          return {}
        }
      },
      opt: function () {
        return {
          url: 'http://localhost:8080/DesktopService',
          params: {
            type: this.ajax_type,
            data: this.ajax_data
          },
          success: this.ajax_success,
          failure: this.ajax_failed
        }
      }
    },
    methods: { // 发送AJAX请求只需要更细ajax_type即可，ajax_data会自动更新
      sub () {
        connect(this.opt)
      },
      regis () {
        this.show_nickname = true
        this.login_title = '注册'
        this.ajax_type = 'USER_REGISTE'
        this.ajax_success = (data) => {
          console.log(data)
          alert(data.message)
          jquery('#closemodel').trigger('click')
          this.uname = ''
          this.psword = ''
          this.nickname = ''
        }
        this.ajax_failed = (data) => {
          console.log(data)
          alert(data.message)
        }
      },
      login () {
        this.show_nickname = false
        this.login_title = '登录'
        this.ajax_type = 'USER_LOGIN'
        this.ajax_success = (data) => {
          console.log(data)
          alert(data.message)
          jquery('#closemodel').trigger('click')
          this.uname = ''
          this.psword = ''
          this.nickname = ''
          this.checklogin()
        }
        this.ajax_failed = (data) => {
          console.log(data)
          alert(data.message)
        }
      },
      checklogin () {
        this.ajax_type = 'USER_CHECK'
        this.ajax_success = this.ajax_failed = (data) => {
          console.log(data)
          this.haslogin = data.success
        }
        connect(this.opt)
      },
      logout () {
        this.ajax_type = 'USER_QUIT'
        this.ajax_success = this.ajax_success = (data) => {
          console.log(data)
          this.checklogin()
        }
        connect(this.opt)
      }
    },
    watch: {
      hidemenubtn: function (oldval, newval) { // 用于在移动端时避免下面的菜单遮盖主导航
        if (newval === false) {
          jquery('#menu-btn').hide()
        } else if (newval === true) {
          jquery('#menu-btn').show()
        }
      }
    },
    created: function () {
      this.checklogin()
    }
  }
</script>

<style scoped>
  .modal-dialog {
    top: 30%;
    padding-left: 50px;
    padding-right: 50px
  }
  #myModalLabel,
  #twobtn {
    text-align: center
  }
</style>



